import { Component, OnInit, Input } from '@angular/core';
import { ApiUrls } from 'src/app/services/api.urls';
import { UserService } from 'src/app/services/authentication/user.service';
import { $$iterator } from 'rxjs/internal/symbol/iterator';


@Component({
  selector: 'app-vbya-chart',
  templateUrl: './vbya-chart.component.html',
  styleUrls: ['./vbya-chart.component.css']
})
export class VbyaChartComponent implements OnInit {

  loading = false;
  totalYValue = 0;
  public chartDataOriginal: any;

  public chartDataShow: any;
  single: any[];
  multi: any[];

  //view: any[] = [1000, 800];

  // options
  showXAxis = true;
  showYAxis = true;
  gradient = false;
  showLegend = false;
  showXAxisLabel = true;
  xAxisLabel = 'Age';
  showYAxisLabel = true;
  yAxisLabel = 'Voter';

  colorScheme = {
    domain: ['#3366cc', '#dc3912', '#ff9900', '#109618', '#990099', '#0099c6', '#dd4477', '#66aa00',
    '#b82e2e', '#316395', '#994499', '#22aa99', '#aaaa11', '#6633cc', '#e67300', '#8b0707']
  };

  showLabels = true;
  explodeSlices = true;
  doughnut = false;
  pieTooltipText = this.piePercentage.bind(this);

  loadTimeout: any;

  round = 10;

  showFilter = true;

  @Input('noFilter') set _noFilter(val) {
    this.showFilter = false;
  }


  filter = {};

  @Input('filter') set ___filter(val) {
    this.filter = val;
    this.load();
  }
  constructor(private userService: UserService) {
    this.load();
  }

  roundChanged(event) {
    clearTimeout(this.loadTimeout);
    this.loadTimeout = setTimeout(() => this.load(parseInt(event.target.value)), 250);
  }

  onFilterChanged(filtR) {
    this.filter = {where: { ...filtR}};
  }
  load(round = 10) {

    this.round = round;
    this.loading = true;
    this.userService.authenticatedGet(ApiUrls.ByModelAggregate(
      'voter', JSON.stringify(this.filter),
       `{$project: {age: {$multiply: [ {$floor: {$divide: ["$meta.age", ${round}]}}, ${round}]}}, $group: {_id: "$age", count: {$sum: 1}}}`)
       ).subscribe(
      (res) => {
        this.chartDataOriginal = res.data.map(a => {
          return {'name': round > 1 ? `${a._id < 18 ? 18 : a._id } - ${a._id + round}` : a._id, 'value': a.count };
        });
        this.chartDataShow = this.chartDataOriginal;

        // for color
        let i = 0;
        this.chartDataOriginal = this.chartDataOriginal.map(function(el) {
          const domain = ['#3366cc', '#dc3912', '#ff9900', '#109618', '#990099', '#0099c6', '#dd4477', '#66aa00',
          '#b82e2e', '#316395', '#994499', '#22aa99', '#aaaa11', '#6633cc', '#e67300', '#8b0707'];
          if (i == 4) {i = 0; }
          const o = Object.assign({}, el);
          o.color = domain[i];
          i++;
          return o;
        });

        this.totalYValue = this.chartDataShow.reduce((a, b) => {
          return a + b.value;
        }, 0);
        this.loading = false;
      }, (e) => this.loading = false);
  }
  onSelect(event) {
    console.log(event);
  }
  onLegendChange($event) {
    this.chartDataShow = $event.map(a => {
      return {'name': a.name, 'value': a.value };
    });
    console.log(this.chartDataShow);
  }
  ngOnInit() {
  }

  piePercentage(e) {
    //console.log(e);
    return `${e.data.name} : ${e.value}(${Math.round( 100 * (e.value / this.totalYValue))}%)`;
  }
}
