import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { MAT_DIALOG_DATA, MatDialogRef, MatSnackBar } from '@angular/material';
import { AddVoterModalComponent } from '../../modals/add-voter-modal/add-voter-modal.component';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-voter-form',
  templateUrl: './voter-form.component.html',
  styleUrls: ['./voter-form.component.css']
})
export class VoterFormComponent implements OnInit {


  closeResult: string;
  authForm: FormGroup;


  constructor(
    private userService: UserService,
    private modalService: NgbModal,
     public formBuilder: FormBuilder,
     @Inject(MAT_DIALOG_DATA) public rowData: any, public dialogRef: MatDialogRef<VoterFormComponent>, private snackBar: MatSnackBar) {


    this.authForm = this.formBuilder.group({
        voterId:  new FormControl({ value: rowData.data.meta.voterId ? rowData.data.meta.voterId : '', disabled: true }),
        meta: this.formBuilder.group({
          fatherName: [rowData.data.meta.fatherName ? rowData.data.meta.fatherName : ''],
          motherName: [rowData.data.meta.motherName ? rowData.data.meta.motherName : ''],
          voterSerialNumber: [rowData.data.meta.voterSerialNumber ? rowData.data.meta.voterSerialNumber : ''],
          husbandName: [rowData.data.meta.husbandName ? rowData.data.meta.husbandName : ''],
          gender: [rowData.data.meta.gender ?
            rowData.data.meta.gender.toLowerCase() === 'female' ? 'female' : 'male'
            : 'male', Validators.required],
          caste: [rowData.data.meta.caste ? rowData.data.meta.caste : ''],
          mobileNumber: [rowData.data.meta.mobileNumber ? rowData.data.meta.mobileNumber : ''],
          name: this.formBuilder.group({
            first: [rowData.data.meta.name.first ? rowData.data.meta.name.first : ''],
            last: [rowData.data.meta.name.last ? rowData.data.meta.name.last : '']
          }),
          houseNumber: [rowData.data.meta.houseNumber ? rowData.data.meta.houseNumber : ''],
        }), /*
        stateBoothId: [rowData.data.stateBoothId ? rowData.data.stateBoothId : '', Validators.compose([Validators.required])],
        stateBoothNumber: [rowData.data.stateBoothNumber ? rowData.data.stateBoothNumber : '', Validators.compose([Validators.required])],
        stateConstituencyId: [rowData.data.stateConstituencyId ? rowData.data.stateConstituencyId : '', Validators.compose([Validators.required])],
        stateConstituencyNumber: [rowData.data.stateConstituencyNumber ? rowData.data.stateConstituencyNumber : '', Validators.compose([Validators.required])],
        stateDistrictId: [rowData.data.stateDistrictId ? rowData.data.stateDistrictId : '', Validators.compose([Validators.required])],
        stateId: [rowData.data.stateId ? rowData.data.stateId : '', Validators.compose([Validators.required])],
        stateMandalId: [rowData.data.stateMandalId ? rowData.data.stateMandalId : '', Validators.compose([Validators.required])],
        stateMandalNumber: [rowData.data.stateMandalNumber ? rowData.data.stateMandalNumber : '', Validators.compose([Validators.required])],
      */      });
    console.log(rowData.data.meta.name.first);
  }


  onSubmit(value) {
    this.userService.authenticatedPatch(ApiUrls.FromModelById('voter', this.rowData.data.id), this.authForm.value, {}).subscribe((res) => {
      if (res.success) {
        this.close();
        this.rowData.data.meta = this.authForm.value.meta;
        this.snackBar.open('Updated', 'X', {
          duration: 2000,
        });
      } else {
        this.snackBar.open('Error: ' + res.error, 'X', {
          duration: 2000,
        });
      }
    });
  }

  close() {
    this.dialogRef.close();
  }




  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  ngOnInit() {
    console.log(this.rowData.data);

  }
}
