import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MatSnackBar } from '@angular/material';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ApiUrls } from '../../../services/api.urls';
import { UserService } from '../../../services/authentication/user.service';


@Component({
  selector: 'app-constituency-form',
  templateUrl: './constituent-form.component.html',
  styleUrls: ['./constituent-form.component.css']
})
export class ConstituencyFormComponent implements OnInit {


  closeResult: string;
  public authForm: FormGroup;


  constructor(private modalService: NgbModal, public formBuilder: FormBuilder,
     public userService: UserService, public dialogRef: MatDialogRef<ConstituencyFormComponent>, private snackBar: MatSnackBar) {

    this.authForm = this.formBuilder.group({
      'name': ['', Validators.compose([Validators.required])],
      'stateId': ['', Validators.compose([Validators.required])],
      'stateDistrictId': ['', Validators.compose([Validators.required])],
      'constituencyNumber': [null, [Validators.required, Validators.min(1)]]
    });
  }

  onSubmit(value: any, form): void {
    if (this.authForm.valid) {
      this.userService.authenticatedPost(ApiUrls.ByModel('stateconstituency'), JSON.stringify(this.authForm.value), {}).subscribe(
        res => {
          this.snackBar.open('Constituency successfully created !', 'X', {
            duration: 4000,
            panelClass: ['success-snackbar'],
            verticalPosition: 'top'
          });
          this.authForm.reset();
        },
        err => {
          console.log('Error occured');
          console.log(err);
          this.snackBar.open('Constituency creation failed !', 'X', {
            duration: 4000,
            panelClass: ['error-snackbar'],
            verticalPosition: 'top'
          });
        }
      );
    }
  }
  /*
  onNoClick(): void {
    this.dialogRef.close();
  }
  */

  closeDialog() {
    this.dialogRef.close();
  }
  /*
  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  */


  ngOnInit() {
  }

}
