import {
  Component,
  OnInit,
  ElementRef,
  ViewChild
} from '@angular/core';
import {
  ApiUrls
} from 'src/app/services/api.urls';
import {
  startWith,
  map
} from 'rxjs/operators';
import {
  MatChipInputEvent,
  MatSnackBar
} from '@angular/material';
import {
  ENTER,
  COMMA
} from '@angular/cdk/keycodes';
import {
  Observable
} from 'rxjs';
import {
  Validators,
  FormControl,
  FormGroup,
  FormBuilder
} from '@angular/forms';
import {
  UserService
} from 'src/app/services/authentication/user.service';
import {
  GridOptions
} from 'ag-grid-community';
import {debounceTime, switchMap} from 'rxjs/operators';

@Component({
  selector: 'app-voter-assignment-modal',
  templateUrl: './voter-assignment-modal.component.html',
  styleUrls: ['./voter-assignment-modal.component.css']
})
export class VoterAssignmentModalComponent implements OnInit {
  @ViewChild('workerInput') workerInput: ElementRef<HTMLInputElement>;
  @ViewChild('voterInput') voterInput: ElementRef<HTMLInputElement>;

  public form: FormGroup;

  userName: string;
  range = '';

  private rowSelection;
  gridOptions: GridOptions = {};
  private gridApi;
  user_chip = new FormControl();
  @ViewChild('user_input') user_input: ElementRef < HTMLInputElement > ;
  voter_chip = new FormControl();
  @ViewChild('user_input') voter_input: ElementRef < HTMLInputElement > ;

  taskTypes: any = ['SMS', 'Share', 'Other']; // ['Facebook', 'Twitter', 'Instagram', 'SMS'];
  user: any = []; // ['Ravinder', 'Ankush','Mayank','Vikas'];
  voters: any = [];
  filteredVoters: any = [];

  displayFn = this.displayFunction.bind(this);

  workerAutocomplete = new FormControl();

  loadingVotersByRange = false;

  /*  displayFn = ((id) => {
    const found = this.currentWorkersList.find(a => a.id === id);
    return found.name.first + ' ' + found.name.last;
  }).bind(this); */


  constructor(private fb: FormBuilder, public userService: UserService, private snackBar: MatSnackBar) {
    this.rowSelection = 'multiple';
  }
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  users_chips: any = [];
  users_chips_view: any = [];
  filteredWorkerOptions: Observable < any[] > ;
  voters_chips: any = [];
  voters_chips_view: any = [];
  voter_filteredOptions: Observable < string[] > ;
  voters_name: any = [];
  selectedVoters: any = [];
  work: any;

  currentWorkersList: any[] = [];

  unitFilter = {
    where: {}
  };

  setUnitFilter(filter) {
    this.unitFilter.where = filter;
    this.workerAutocomplete.reset();
    while (this.filteredVoters.length) {
      this.filteredVoters.pop();
    }

    while (this.voters_chips_view.length) {
      this.voters_chips_view.pop();
    }

    while (this.voters_chips.length) {
      this.voters_chips.pop();
    }
  }

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || '').trim()) {
      this.users_chips.push(value);
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }
  }
  onSelectionChanged() {

    const selectedRows = this.gridApi.getSelectedRows();
    this.selectedVoters = [];
    selectedRows.forEach(element => {

      this.selectedVoters.push(element.id);

    });


  }

  addVoter(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;
    if (input) {
      input.value = '';
    }
  }


  removeVoter(val: any): void {
    const index = this.voters_chips.indexOf(val.id);
    if (index >= 0) {
      this.voters_chips.splice(index, 1);
    }

    const chipIndex = this.voters_chips_view.indexOf(val);
    if (index >= 0) {
      this.voters_chips_view.splice(chipIndex, 1);
    }
  }


  ngOnInit() {
    this.form = this.fb.group({
      'selectVoterOpt': [null],
      'userchip': [null],
      'tasktitle': [null],
      'taskdescription': [null],
      'voterchip': [null],
      'range': [null],
      'type': [null, Validators.compose([Validators.required])],
      'link': [null],
      'textMsg': [null],
      'image': [null],
      'numbers': [null],
      'dueDate': [null]
    });

    this.filteredWorkerOptions = this.workerAutocomplete.valueChanges.pipe(
      debounceTime(300),
      switchMap(value => this.searchFilter(value))
    );

    this.voter_filteredOptions = this.voter_chip.valueChanges.pipe(
      debounceTime(300),
      switchMap(value => this.voterSearchFilter(value))
    );
  }
  private searchFilter(event): Observable<any[]> {
    console.log(this.unitFilter);
    const filterValue = event; // .target.value.toLowerCase();
    const ret = this.userService.authenticatedGet(ApiUrls.ByModel('user',
     JSON.stringify({where: {
      ...this.unitFilter.where,
      'name.first': {$regex: `^.*${filterValue}.*`, $options: 'i'}}})))
      .map((res): any[] => {
        if (res.success) {
          this.currentWorkersList = res.data;
          return res.data;
        } else {
          return [];
        }
      });
      return ret;
  }
  private voterSearchFilter(event): Observable<any[]> {
    const filterValue = event; // .target.value.toLowerCase();
    const ret = this.userService.authenticatedGet(ApiUrls.ByModel('voter',
    JSON.stringify({where: {
      ...this.unitFilter.where,
      $or: [{'meta.name.first': {$regex: `^.*${filterValue}.*`, $options: 'i'}},
            {'meta.name.last': {$regex: `^.*${filterValue}.*`, $options: 'i'}}
      ]
    }})))
      .map((res): any[] => {
        if (res.success) {
          this.voters = res.data;
          return res.data;
        } else {
          return [];
        }
      });
      return ret;
  }


  private v_filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.voters.filter(option => option.toLowerCase().indexOf(filterValue) === 0);
  }

  private _filter(value: any): string[] {
    const filterValue = value.toLowerCase();
    return this.user.filter(option => (option.name.first + ' ' + option.name.last).toLowerCase().indexOf(filterValue) === 0);
  }
  vselected(event: any): void {
    this.selectedVoters = [];
    if (!( < Array < any >> this.voters_chips).find(a => a === event.option.value)) {

      this.voters_chips.push(event.option.value);
      this.voters_chips_view.push({name: event.option.viewValue, id: event.option.value});
      this.voter_chip.setValue(null);
      console.log(this.voters_chips);

      this.voters_chips.forEach(element => {
        this.selectedVoters.push(element);
      });
    }
    this.voterInput.nativeElement.value = '';
  }

  onUserSelect(select: any) {
    console.log('Hi');
    const obj = this.form.value;
    console.log(obj);
    for (const x in obj.user) {
      if (!((this.users_chips).includes(obj.user[x]))) {
        this.users_chips.push(obj.user[x]);
      }
    }
  }

  rangeActive(event: any) {
    this.loadingVotersByRange = true;
    let parts: any = [];
    let skipN: any;
    let limitN: any;
    this.range = event.target.value;
    parts = this.range.split('-');
    skipN = parts[0] - 1;
    limitN = parts[1];
    while (this.filteredVoters.length > 0) {
      this.filteredVoters.pop();
    }
    this.userService.authenticatedGet(ApiUrls.ByModel('voter',
      JSON.stringify(this.unitFilter), skipN, limitN), {}).subscribe((res) => {
        res.data.forEach(element => {
          this.filteredVoters.push({
            'id': element.id,
            'firstName': element.meta.name.first,
            'lastName': element.meta.name.last,
            'fatherName': element.meta.fatherName,
            'houseNo': element.meta.houseNumber
          });
          this.loadingVotersByRange = false;
        }, e => {
          this.loadingVotersByRange = false;
        });
      });
  }

  columnDefs = [{
      headerName: '',
      field: '',
      headerCheckboxSelection: true,
      'checkboxSelection': true,
      sortable: true,
      filter: true
    },
    {
      headerName: 'First Name',
      field: 'firstName',
      sortable: true,
      filter: true
    },
    {
      headerName: 'Last Name',
      field: 'lastName',
      sortable: true,
      filter: true
    },
    {
      headerName: 'Father`s Name',
      field: 'fatherName',
      sortable: true,
      filter: true
    },
    {
      headerName: 'House No',
      field: 'houseNo',
      sortable: true,
      filter: true
    },
  ];


  private imageSrc = '';

  handleInputChange(e) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    const pattern = /image-*/;
    const reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    const reader = e.target;
    this.imageSrc = reader.result;
    console.log(this.imageSrc);
    this.form.value.image = this.imageSrc;
    this.work.image = this.imageSrc;
  }


  linkChange(e) {
    this.work.link = e.target.value;

    console.log(this.work.link);
  }

  textChange(e) {
    this.work.text = e.target.value;

    console.log(this.work.link);

  }

  onSubmit(value: any) {
    if (this.form.value.selectVoterOpt === 'Range') {
      this.form.value.voterchip = this.range;
      this.form.value.userchip = this.users_chips;
    } else {
      this.form.value.userchip = this.users_chips;
      this.form.value.voterchip = this.voters_chips;
    }

    console.log(value);
    const element = this.workerAutocomplete.value;
    this.userService.authenticatedGet(ApiUrls.ByModel('smsvoterassignment',
      JSON.stringify({
        where: {
          userId: element
        }
      }))).flatMap((res) => {
      if (res.success && res.data[0]) {
        return this.userService.authenticatedPatch(
          ApiUrls.FromModelById('smsvoterassignment', res.data[0].id),
          JSON.stringify({
            userId: element,
            stateBoothId: this.form.value.stateBoothId,
            voters: this.selectedVoters
          }), {});
      } else {
        return this.userService.authenticatedPost(
          ApiUrls.ByModel('smsvoterassignment'),
          JSON.stringify({
            userId: element,
            stateBoothId: this.form.value.stateBoothId,
            voters: this.selectedVoters
          }), {});
      }}).subscribe( res => {
          this.snackBar.open('Successfully assigned!', 'X', {
            duration: 4000,
          });
          this.form.reset();
          delete this.form.value['selectVoterOpt'];
          delete this.form.value['range'];
          delete this.form.value['selectVoterOpt'];
          this.voter_chip.reset();
          this.voters_chips_view = [];
          this.selectedVoters = [];
          this.filteredVoters = [];
        },
        err => {
          this.snackBar.open('Assignment failed !', 'X', {
            duration: 4000});
          }
      );
  }

  onGridReady(params) {
    this.gridApi = params.api;
  }

  displayFunction(id ?: string): string {
    const item = this.currentWorkersList.find(a => a.id === id);
    return item && item.name ? item.name.first + ' ' + item.name.last : undefined;
  }
}
