import { Component, OnInit, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-manage-application-phases',
  templateUrl: './manage-application-phases.component.html',
  styleUrls: ['./manage-application-phases.component.css']
})
export class ManageApplicationPhasesComponent implements AfterViewInit {

  phases = [{
    label: 'Voter data correction',
    value: 'voters'
  }, {
    label: 'House tagging',
    value: 'housetagging'
  }, {
    label: 'Polling day',
    value: 'pollingday'
  }];
  public applicationPhase: FormGroup = this.formBuilder.group({
   /*  'voters': [false, Validators.required],
    'housetagging': [false, Validators.required],
    'pollingday': [false, Validators.required], */
    'phases': [null, Validators.required]
  });

  constructor(private formBuilder: FormBuilder,
    private userService: UserService, private changeDetectorRef: ChangeDetectorRef, private snackbar: MatSnackBar) {
  }

  ngAfterViewInit() {
    this.userService.authenticatedGet(ApiUrls.FromModelById('platformconfiguration', ApiUrls.InstanceId)).subscribe((res) => {
      const phases: string[] = res.data.phases;
      /* this.applicationPhase.setValue(res.data.phases.map((a) => {
        return {a: true};
      }).reduce((a, b) => {
        return {a, ...b};
      })); */
      this.applicationPhase.patchValue({
        'phases': phases,
/*         'voters': phases.includes('voters'),
        'housetagging': phases.includes('housetagging'),
        'pollingday': phases.includes('pollingday')
 */      });
//      this.changeDetectorRef.detectChanges();
    });
  }

  submit(event) {
    this.userService.authenticatedPatch(ApiUrls.ByModel('platformconfiguration'), this.applicationPhase.value, {}).subscribe((res) => {
      this.snackbar.open('Updated');
    });
  }

}
