import { Component, OnInit, Input } from '@angular/core';
import { ApiUrls } from 'src/app/services/api.urls';
import { UserService } from 'src/app/services/authentication/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-per-user-task-counter',
  templateUrl: './per-user-task-counter.component.html',
  styleUrls: ['./per-user-task-counter.component.css']
})
export class PerUserTaskCounterComponent implements OnInit {

  @Input() userId;

  counter: number;
  doneCounter: number;

  otherCounter: {total, done} = {total: 0, done: 0};

  shareCounter: {total, done} = {total: 0, done: 0};
  callCounter: {total, done} = {total: 0, done: 0};

  smsCounter: {total, done} =  {total: 0, done: 0};

  constructor(public userService: UserService, private router: Router) {
  }

  ngOnInit() {
    this.load();
  }

  load() {
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count', JSON.stringify({where: {assignedTo: this.userId}})
    )).subscribe((res) => {
        this.counter = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, isDone: true}}))).subscribe((res) => {
      this.doneCounter = res.data;
    });

    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, taskType: 'Call'}}))).subscribe((res) => {
      this.callCounter['total'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, isDone: true, taskType: 'Call'}}))).subscribe((res) => {
      this.callCounter['done'] = res.data;
    });

    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, isDone: true, taskType: 'SMS'}}))).subscribe((res) => {
      this.smsCounter['done'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, taskType: 'SMS'}}))).subscribe((res) => {
      this.smsCounter['total'] = res.data;
    });

    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, isDone: true, taskType: 'Share'}}))).subscribe((res) => {
      this.shareCounter['done'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, taskType: 'Share'}}))).subscribe((res) => {
      this.shareCounter['total'] = res.data;
    });


    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, isDone: true, taskType: 'Other'}}))).subscribe((res) => {
      this.otherCounter['done'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {assignedTo: this.userId, taskType: 'Other'}}))).subscribe((res) => {
      this.otherCounter['total'] = res.data;
    });
  }
  btn() {
    this.router.navigateByUrl('manage/workers');
  }
}
