import {
  Component,
  OnInit
} from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators
} from '@angular/forms';
import {
  MatDialogRef,
  MatSnackBar
} from '@angular/material';
import {
  Papa
} from 'ngx-papaparse';
import {
  ApiUrls
} from 'src/app/services/api.urls';
import {
  UserService
} from 'src/app/services/authentication/user.service';

@Component({
  selector: 'app-csvupload-user-modal',
  templateUrl: './csvupload-user-modal.component.html',
  styleUrls: ['./csvupload-user-modal.component.css']
})
export class CSVUploadUserModalComponent implements OnInit {

  file = null;
  parsedJson: any;
  public form: FormGroup;
  formSubmitted = false;
  progress = 0;
  prog = 0;
  votersLength;
  increment;
  items: Array < {
    id,
    name
  } > = [];


  constructor(public papa: Papa, public userService: UserService,
    public snackBar: MatSnackBar, public formBuilder: FormBuilder,
    public dialogRef: MatDialogRef < CSVUploadUserModalComponent > ) {
    this.form = this.formBuilder.group({
      'stateId': ['', Validators.compose([Validators.required])],
      'stateConstituencyId': ['', Validators.compose([Validators.required])],
      // 'roles': [null, Validators.compose([Validators.required])]
    });
  }


  ngOnInit() {}
  addVoters(formvalue) {
    if (!this.file) {
      this.snackBar.open('Please add a file first!','Ok', {
        duration: 3000
      });
      return;
    }
    const form = new FormData();
    form.append('file', this.file);
    this.userService.authenticatedPost(ApiUrls.ByModel('user/import/' + this.form.value.stateConstituencyId),
     form, {ContentType: ' '}).subscribe((res) => {
      this.snackBar.open('Successfully uploaded, Worker will appear under manage/Worker in few minutes','Ok', {
        duration: 3000
      });
      this.dialogRef.close();
    });
  }

  
  closeDialog() {
    this.dialogRef.close();
  }


  handleInputChange(e) {
    this.file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
/*     this.papa.parse(file, {
      complete: (result) => {

        this.parsedJson = result.data;
        console.log('Parsed: ', this.parsedJson);
      }
    });
 */  }

}
