import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ITreeState, ITreeOptions } from 'angular-tree-component';
import { v4 } from 'uuid';
import { nodeChildrenAsMap } from '@angular/router/src/utils/tree';

@Component({
  selector: 'app-legend',
  templateUrl: './legend.component.html',
  styleUrls: ['./legend.component.css']
})
export class LegendComponent implements OnInit {
  @Input() graphData: any;

  @Output() legendChangeEvent = new EventEmitter<object>();

  public nodes = [];

  constructor() {
  }
  options: ITreeOptions = {
    allowDrag: (node) => {
      //console.log(node.level);
      return node.isLeaf;
    },
    allowDrop: (element, { parent, index }) => {
      return !(parent.level > 1);
    }
  };

  onMoveNode($event) {
    const obj = $event.to.parent;
    let total_val = 0;
    obj.children.forEach(function(item) {
      //copy.push(item*item);
      total_val += item.value;
    });
    obj.value = total_val + (obj.value - (total_val - $event.node.value)); // total of children + value of parent

    this.legendChangeEvent.emit(this.graphData);
     console.log($event.to.parent);
  }

  ngOnInit() {
  }

}
