import { Component, OnInit } from '@angular/core';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';
import { UserService } from '../../../services/authentication/user.service';
import { UsersFormComponent } from '../users-form/users-form.component';
import { MatDialogRef, MatSnackBar } from '@angular/material';
import { ApiUrls } from '../../../services/api.urls';

@Component({
  selector: 'app-booth-form',
  templateUrl: './booth-form.component.html',
  styleUrls: ['./booth-form.component.css']
})
export class BoothFormComponent implements OnInit {


  closeResult: string;
  public authForm: FormGroup;


  constructor(private modalService: NgbModal, public formBuilder: FormBuilder, public userService: UserService,
     public dialogRef: MatDialogRef<UsersFormComponent>, private snackBar: MatSnackBar) {

    this.authForm = this.formBuilder.group({
      'name': ['', Validators.compose([Validators.required])],
      'stateId': ['', Validators.compose([Validators.required])],
      'stateDistrictId': ['', Validators.compose([Validators.required])],
      'stateConstituencyId': ['', Validators.compose([Validators.required])],
      'stateMandalId': [null, Validators.compose([Validators.required])],
      'stationNumber': ['', Validators.compose([Validators.required])],
      'location': ['', Validators.compose([Validators.required])],
    });
  }
  onSubmit(value: any): void {
    if(this.authForm.valid) {
      this.userService.authenticatedPost(ApiUrls.ByModel('statebooth'), JSON.stringify(this.authForm.value), {}).subscribe(
        res => {
          this.snackBar.open('Booth successfully created !', 'X', {
            duration: 4000,
            panelClass: ['success-snackbar'],
            verticalPosition: 'top'
          });
          this.authForm.reset();
        },
        err => {
          console.log("Error occured");
          console.log(err);
          this.snackBar.open('Booth creation failed !', 'X', {
            duration: 4000,
            panelClass: ['error-snackbar'],
            verticalPosition: 'top'
          });
        }
      );
    }
  }
/*
  onNoClick(): void {
    this.dialogRef.close();
  }
  */

  closeDialog(){
    this.dialogRef.close();
  }


  /*
  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  */


  ngOnInit() {
  }
}
