import { Component, OnInit, Input } from '@angular/core';
import { filter } from 'rxjs-compat/operator/filter';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-voter-counter',
  templateUrl: './voter-counter.component.html',
  styleUrls: ['./voter-counter.component.css']
})
export class VoterCounterComponent implements OnInit {
  @Input() label = '';

  showFilter = true;

  @Input('noFilter') set _noFilter(val) {
    this.showFilter = false;
  }


  filter = {};

  @Input('filter') set ___filter(val) {
    this.filter = val;
    this.load();
  }

  counter = 0;

  timer = null;
  constructor(private userService: UserService) {
  }
  ngOnInit() {
    // this.load();
  }
  load() {
    this.userService.authenticatedGet(ApiUrls.ByModel('voter/count', JSON.stringify(this.filter))).subscribe((res) => {
      this.counter = res.data;
    });
  }

  onFilterChanged(filtR) {
    clearTimeout(this.timer);
    this.filter = filtR;
    this.timer = setTimeout(() => this.load(), 500);
  }
}
