import { Component, Input, OnInit, OnDestroy } from "@angular/core";
import { Observable } from "rxjs";

@Component({
  selector: "app-print-user-name",
  templateUrl: "./print-user-name.component.html",
  styleUrls: ["./print-user-name.component.scss"]
})
export class PrintUserNameComponent implements OnInit, OnDestroy {
  @Input("name") name;
  @Input("id") id;
  @Input("showStatus") showStatus = true;
  @Input() firstNameOnly = false;
  @Input() fontSize = "14px";

  constructor() {}

  ngOnInit() {}

  ngOnDestroy() {}
}
