import { Component, OnInit } from "@angular/core";
import { AddBoothModalComponent } from "../../modals/add-booth-modal/add-booth-modal.component";
import { MatDialog } from "@angular/material";

@Component({
  selector: "app-add-booth-button",
  templateUrl: "./add-booth-button.component.html",
  styleUrls: ["./add-booth-button.component.css"]
})
export class AddBoothButtonComponent implements OnInit {
  constructor(private dialog: MatDialog) {}

  ngOnInit() {}

  open($event) {
    let dialogRef = this.dialog.open(AddBoothModalComponent, {
      width: "450px"
    });

    /*
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`); // Pizza!
    });

    dialogRef.close();
    */
  }
}
