import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import {
  ApiUrls
} from 'src/app/services/api.urls';
import {
  UserService
} from 'src/app/services/authentication/user.service';
@Component({
  selector: 'app-auto-voter-assignment-modal',
  templateUrl: './auto-voter-assignment-modal.component.html',
  styleUrls: ['./auto-voter-assignment-modal.component.css']
})
export class AutoVoterAssignmentModalComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<AutoVoterAssignmentModalComponent>, private userService: UserService, private snackBar: MatSnackBar) { }
  filter = {};
  clearFilters;
  ngOnInit() {
  }
  close() {
    this.dialogRef.close();
  }
  onFilterChanged(filter) {
    console.log(filter);

    this.filter = filter;
  }


  assignVoter() {
    this.userService.authenticatedPost(ApiUrls.ByModel('user/auto-voter-assignment',
     JSON.stringify({where: this.filter})), {}, {}).subscribe((res) => {
      this.snackBar.open('Voter assignments will be taken care of, you can continue your other work!', 'Ok');
      this.close();
    });
  }


}
