import { Component, Input, OnInit } from "@angular/core";
import {
  AppServerResponse,
  AppUser,
  UserData,
  UserService
} from "../../../services/authentication/user.service";
import { ApiUrls } from "../../../services/api.urls";

@Component({
  selector: "app-print-user",
  templateUrl: "./print-user.component.html",
  styleUrls: ["./print-user.component.css"]
})
export class PrintUserComponent implements OnInit {
  assignedTo: string;
  @Input('assignedTo') set _assignedTo(val: string){
    this.assignedTo = val;
    this.load();
  }
  @Input() multiLine = false;
  @Input() profilePicOnly = false;
  @Input() onlineStatus = true;
  @Input() firstNameOnly = false;
  loading = true;
  @Input() user: UserData;
  @Input() profilePictureDimension = 64;
  apiUrls = ApiUrls;
  @Input() fontSize = "14px";

  constructor(private userService: UserService) {}

  ngOnInit() {
    if (this.user) {
      this.loading = false;
    }
  }
  load() {
    this.loading = true;
      this.userService.getUser(this.assignedTo).subscribe((userData) => {
          this.user = userData;
          this.loading = false;
        });
  }
}
