import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material';
import { Papa } from 'ngx-papaparse';
import { ApiUrls } from 'src/app/services/api.urls';
import { UserService } from 'src/app/services/authentication/user.service';

declare var window;

@Component({
  selector: 'app-import-data',
  templateUrl: './import-data.component.html',
  styleUrls: ['./import-data.component.css']
})


export class ImportDataComponent implements OnInit {

  formVoter = this.formBuilder.group({
    'stateId': ['', Validators.compose([Validators.required])],
    'constituencyId': ['', Validators.compose([Validators.required])]
  });
  parsedJson: any = [];
  states: any = [];
  filteredStates: any = [];
  newStates: any = {};
  districtUploadStart = false;
  constituencyDataUploadStart = false;
  voterDataUploadStart = false;
  progress = 0;
  viewProgress = 0;
  public authForm: FormGroup;

  stateIds: any = [];


  constructor(public papa: Papa, public userService: UserService, public snackBar: MatSnackBar, public formBuilder: FormBuilder, ) {
    this.authForm = this.formBuilder.group({
      'stateId': ['', Validators.compose([Validators.required])]
    });
  }

  ngOnInit() {}

  handleDistrictInputChange(e) {
    this.progress = 0;
    this.viewProgress = 0;

    this.districtUploadStart = true;
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    console.log(file);
    this.papa.parse(file, {
      complete: (result) => {

        this.parsedJson = result.data;

        this.parsedJson.forEach((element, i) => {
            if (i === 0) {
                return;
            }
            this.states.push(element[1]);
        });

        this.filteredStates = this.states.filter((item, pos, ar) => ar.indexOf(item) === pos);
        this.filteredStates.forEach(element => {
          const state = element;
          const newCities = [];
          this.parsedJson.forEach(element => {
            if (state === element[1]) {
              newCities.push(element[3]);
            }
          });
          if (state) {
            this.newStates[state.trim()] = newCities;
          }
        });
        const addprog = 100 / (this.filteredStates.length - 1);

        console.log(addprog, this.newStates);

        Object.keys(this.newStates).forEach((key) => {
          this.userService.authenticatedGet(ApiUrls.ByModel('state', '{where: {name:"' + key + '"}}'), {}).subscribe((res) => {

            if (res.data.length > 0) {
              this.newStates[key].forEach(element => {

                this.userService.authenticatedPost(ApiUrls.ByModel('statedistrict'), {
                  'stateId': res.data[0].id,
                  'name': element
                }, {}).subscribe((res) => {
                  console.log(res);
                  this.progress = this.progress + addprog;
                  this.viewProgress = Math.trunc(this.progress);
                  console.log(this.viewProgress);
                });
              });

            } else {

              this.userService.authenticatedPost(ApiUrls.ByModel('state'), {
                'name': key
              }, {}).subscribe((res) => {
                this.newStates[key].forEach(element => {

                    this.userService.authenticatedPost(ApiUrls.ByModel('statedistrict'), {
                      'stateId': res.data.id,
                      'name': element
                    }, {}).subscribe((res) => {
                      console.log(res);
                      this.progress = this.progress + addprog;
                      this.viewProgress = Math.trunc(this.progress);
                      console.log(this.viewProgress);
                    });
                  });
              });
            }
          });
        });
      }
    });
  }




  handleConstituencyInputChange(e) {
      const constituencies = {};
      const mandals = {};

    this.constituencyDataUploadStart = true;
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    //console.log(file);
    this.papa.parse(file, {
      complete: (result) => {
            this.parsedJson = result.data;
            console.log(this.parsedJson);
            // delete this.parsedJson[0];
            console.log(this.parsedJson);
            const addprog = 100 / (this.parsedJson.length - 1);

            this.parsedJson.forEach((element, i) => {
                if (i === 0) {
                    return;
                }
                if (constituencies[element[3]] === undefined) {
                    constituencies[element[3]] = {
                        district: '',
                        number: 0,
                        mandals: []
                    };
                }
                if (mandals[element[5]] === undefined) {
                  mandals[element[5]] = [];
                  constituencies[element[3]].district = element[1];
                  constituencies[element[3]].number = element[2];
                  constituencies[element[3]].mandals.push({mandalId : element[4], name: element[5]});
                }
                mandals[element[5]].push({name: element[9], id: element[8]});
            });


            const cKeys = Object.keys(constituencies);
            console.log('No. of constituency', cKeys.length);
            cKeys.forEach((constituency) => {
                this.userService.authenticatedGet(ApiUrls.ByModel('statedistrict',
                '{where:{name:"' + constituencies[constituency].district +
                    '", stateId:"' + this.authForm.value.stateId + '"}}')
                , {}).subscribe((dres) => {
                    console.log(dres);
                    if (dres.success && dres.data.length > 0) {
                        this.userService.authenticatedPost(ApiUrls.ByModel('stateconstituency'), {
                        'name': constituency,
                        'constituencyNumber': Number(constituencies[constituency].number),
                        'stateDistrictId': dres.data[0].id,
                        'stateId': this.authForm.value.stateId
                        }, {}).subscribe((cres) => {
                            if (cres.success) {
                                constituencies[constituency].mandals.forEach((mandal) => {
                                        this.userService.authenticatedPost(ApiUrls.ByModel('statemandal'), {
                                            'name': mandal.name,
                                            'mandalNumber': Number(mandal.mandalId),
                                            'stateConstituencyId': cres.data.id,
                                            'constituencyNumber': Number(constituencies[constituency].number),
                                            'stateDistrictId': dres.data[0].id,
                                            'stateId': this.authForm.value.stateId
                                        }, {}).subscribe((mres) => {
                                              if (mres.success) {
                                                mandals[mandal.name].forEach((booth) => {
                                                    this.userService.authenticatedPost(ApiUrls.ByModel('statebooth'), {
                                                        'name': booth.name,
                                                        'boothNumber': Number(booth.id),
                                                        'mandalNumber': Number(mandal.mandalId),
                                                        'stateConstituencyId': cres.data.id,
                                                        'constituencyNumber': Number(constituencies[constituency].number),
                                                        'stateDistrictId': dres.data[0].id,
                                                        'stateMandalId': mres.data.id,
                                                        'stateId': this.authForm.value.stateId
                                                    }, {}).subscribe((mres) => {
                                                        this.progress = this.progress + addprog;
                                                        this.viewProgress = Math.trunc(this.progress);
                                                    });
                                                });
                                            }
                                        });
                                });
                            } else {
                                alert(cres.error);
                            }
                        });
                    } else {
                        alert('District ' + constituencies[constituency].district + 'not found');
                    }
                });
            });

      }
    });
  }
}
