import { Component, OnInit } from "@angular/core";
import { AddMandalModalComponent } from "../../modals/add-mandal-modal/add-mandal-modal.component";
import { MatDialog } from "@angular/material";

@Component({
  selector: "app-add-mandal-button",
  templateUrl: "./add-mandal-button.component.html",
  styleUrls: ["./add-mandal-button.component.css"]
})
export class AddMandalButtonComponent implements OnInit {
  constructor(private dialog: MatDialog) {}

  ngOnInit() {}

  open($event) {
    let dialogRef = this.dialog.open(AddMandalModalComponent, {
      width: "450px"
    });

    /*
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`); // Pizza!
    });

    dialogRef.close();
    */
  }
}
