import { Component, OnInit, Input } from '@angular/core';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-vbyc-chart',
  templateUrl: './vbyc-chart.component.html',
  styleUrls: ['./vbyc-chart.component.css']
})
export class VbycChartComponent implements OnInit {
  totalYValue = 0;
  public chartDataOriginal: any[];

  public chartDataShow: any[];
  single: any[];
  multi: any[];

  //view: any[] = [750, 500];

  // options
  showXAxis = true;
  showYAxis = true;
  gradient = false;
  showLegend = false;
  showXAxisLabel = true;
  xAxisLabel = 'Caste';
  showYAxisLabel = true;
  yAxisLabel = 'Voter';

  colorScheme = {
    domain: ['#3366cc', '#dc3912', '#ff9900', '#109618', '#990099', '#0099c6', '#dd4477', '#66aa00',
    '#b82e2e', '#316395', '#994499', '#22aa99', '#aaaa11', '#6633cc', '#e67300', '#8b0707']
  };

  showLabels = true;
  explodeSlices = false;
  doughnut = false;
  pieTooltipText = this.piePercentage.bind(this);

  showFilter = true;

  @Input('noFilter') set _noFilter(val) {
    this.showFilter = false;
  }


  filter = {};

  @Input('filter') set ___filter(val) {
    this.filter = val;
    this.load();
  }
  loading = true;
  constructor(private userService: UserService) {
    this.load();
  }

  load() {
    this.loading = true;
    this.userService.authenticatedGet(ApiUrls.ByModelAggregate(
      'voter', JSON.stringify(this.filter), '{$group: {_id: "$meta.caste", count: {$sum: 1}}}'), {}).subscribe(
      (res) => {
        console.log(res.data);
        this.chartDataOriginal = res.data.map(a => {
          return {'name': a._id ? a._id : 'Unknown', 'value': a.count };
        });
        this.chartDataShow = this.chartDataOriginal;

        // for color
        let i = 0;
        this.chartDataOriginal = this.chartDataOriginal.map(function(el) {
          const domain = ['#3366cc', '#dc3912', '#ff9900', '#109618', '#990099', '#0099c6', '#dd4477', '#66aa00',
          '#b82e2e', '#316395', '#994499', '#22aa99', '#aaaa11', '#6633cc', '#e67300', '#8b0707'];
          if (i == 4) {i = 0; }
          const o = Object.assign({}, el);
          o.color = domain[i];
          i++;
          return o;
        });

        this.totalYValue = this.chartDataShow.reduce((a, b) => {
          return a + b.value;
        }, 0);
        this.totalYValue = !this.totalYValue ? 0 : this.totalYValue;
        this.loading = false;
      });
  }
  onSelect(event) {
    console.log(event);
  }

  onFilterChanged(filtR) {
    this.filter = {where: { ...filtR}};
  }

  onLegendChange($event) {
    this.chartDataShow = $event.map(a => {
      return {'name': a.name, 'value': a.value };
    });
    console.log(this.chartDataShow);
  }

  ngOnInit() {
  }

  piePercentage(e) {
    return `${e.data.name} : ${e.value}(${Math.round( 100 * (e.value / this.totalYValue))}%)`;
  }
}
