import { Routes } from '@angular/router';
import { UsersComponent } from './users/users.component';
import { VotersComponent } from './voters/voters.component';
import { MandalsComponent } from './mandals/mandals.component';
import { TrackComponent } from './track/track.component';
import { GeneralSettingsComponent } from './general-settings/general-settings.component';
import { UpdateProfileComponent } from './update-profile/update-profile.component';
import { ConstituencyComponent } from './constituency/constituency.component';
import { BoothComponent } from './booth/booth.component';
import { TasksComponent } from './tasks/tasks.component';
import { TrackLocationComponent } from './track-location/track-location.component';
import { DistrictComponent } from './district/district.component';
import { VoterAssignmentModalComponent } from './voter-assignment-modal/voter-assignment-modal.component';
import { ImportDataComponent } from './import-data/import-data.component';
import { ReportsComponent } from './reports/reports.component';


export const PageRoutes: Routes = [
  {
    path: '',
    children: [
      {
        path: '',
        component: TrackComponent,
        data: {
          title: 'Track Booth Worker',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            {
              title: 'Track Booth Worker'
            }
          ]
        }
      },
      {
        path: 'track',
        component: TrackComponent,
        data: {
          title: 'Track Booth Worker',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            {
              title: 'Track Booth Worker'
            }
          ]
        }
      },
      {
        path: 'track/:id',
        component: TrackLocationComponent,
        data: {
          title: 'Track Booth Worker',
          urls: [
            { title: 'Track', url: '/track' },
            {
              title: 'Track Booth Worker'
            }
          ]
        }
      },


      {
        path: 'booths',
        component: BoothComponent,
        data: {
          title: 'Booths',
          urls: [{ title: 'Dashboard', url: '/dashboard' }, { title: 'Booths' }]
        }
      },

      {
        path: 'tasks',
        component: TasksComponent,
        data: {
          title: 'Tasks',
          urls: [{ title: 'Dashboard', url: '/dashboard' }, { title: 'Tasks' }]
        }
      },
      {
        path: 'workers',
        component: UsersComponent,
        data: {
          title: 'Workers',
          urls: [{ title: 'Dashboard', url: '/dashboard' }, { title: 'Users' }]
        }
      },
      {
        path: 'districts',
        component: DistrictComponent,
        data: {
          title: 'Districts',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            {
              title: 'Districts'
            }
          ]
        }
      },
      {
        path: 'constituencies',
        component: ConstituencyComponent,
        data: {
          title: 'Constituencies',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            {
              title: 'Constituents'
            }
          ]
        }
      },
      {
        path: 'voters',
        component: VotersComponent,
        data: {
          title: 'Voters',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            {
              title: 'Voters'
            }
          ]
        }
      },
      {
        path: 'mandals',
        component: MandalsComponent,
        data: {
          title: 'Mandals',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            {
              title: 'Mandals'
            }
          ]
        }
      },
      {
        path: 'voter-assignment',
        component: VoterAssignmentModalComponent,
        data: {
          title: 'Voter Assignment',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            {
              title: 'Voter Assignment'
            }
          ]
        }
      },
  {
        path: 'reports',
        component: ReportsComponent,
        data: {
          title: 'Reports',
          urls: [
            { title: 'Reports', url: '/reports' },
            {
              title: 'Reports'
            }
          ]
        }
      },
      {
        path: 'import',
        component: ImportDataComponent
      },
      {
        path: 'general-settings',
        component: GeneralSettingsComponent,
        data: {
          title: 'General Settings',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            { title: 'General Settings' }
          ]
        }
      },
      {
        path: 'update-profile',
        component: UpdateProfileComponent,
        data: {
          title: 'Update Profile',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            { title: 'Update Profile' }
          ]
        }
      }
    ]
  }
];
