import {
  Component,
  OnInit,
  ViewChild,
  ElementRef
} from '@angular/core';

import {
  UserService
} from '../../../services/authentication/user.service';

import {
  FormArray,
  FormControl,
  FormBuilder,
  FormGroup,
  Validators,
  FormGroupDirective,
  NgForm
} from '@angular/forms';

import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatSnackBar,
  ErrorStateMatcher
} from '@angular/material';

import {
  MatChipInputEvent
} from '@angular/material';
import {
  COMMA,
  ENTER
} from '@angular/cdk/keycodes';
import {
  Observable
} from 'rxjs';
import {
  debounceTime,
  switchMap
} from 'rxjs/operators';
import {
  ApiUrls
} from 'src/app/services/api.urls';
import {
  bubble
} from 'src/app/charts/ngx-charts/chartData';

/*
export class ChoiceRangeErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    return !!(control && control.invalid && (control.dirty || control.touched));
  }
}*/

@Component({
  selector: 'app-task-form',
  templateUrl: './task-form.component.html',
  styleUrls: ['./task-form.component.css']
})
export class TaskFormComponent implements OnInit {
  @ViewChild('workerInput') workerInput: ElementRef < HTMLInputElement > ;

  public form: FormGroup;

  userName: string;
  range = '';

  user_chip = new FormControl();
  voter_chip = new FormControl();

  taskTypes: any = ['SMS', 'Share', 'Call', 'Other']; // ['Facebook', 'Twitter', 'Instagram', 'SMS'];
  selectedWorkers: any = [];
  voters: any = [];
  filteredVoters: any = [];

  visible = true;
  selectable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  workersChipsVisible: any = [];

  workers: any[] = [];
  filteredWorkerOptions: Observable < any[] > ;
  numbers: any = [];
  work: any;

  workerAutocomplete = new FormControl();

  unitFilter = {
    where: {}
  };

  setUnitFilter(filter) {
    this.unitFilter.where = filter;
    this.selectedWorkers = [];
    this.workersChipsVisible = [];
  }

  assignmentChoice = '1';
  bulkAssignmentChoice = '1';
  bulkAssignmentRange: string;

  constructor(
    private fb: FormBuilder,
    public dialogRef: MatDialogRef < TaskFormComponent > ,
    public userService: UserService,
    private snackBar: MatSnackBar
  ) {}

  remove(worker: any): void {
    const index = this.selectedWorkers.indexOf(worker.id);

    if (index >= 0) {
      this.selectedWorkers.splice(index, 1);
    }

    const chipIndex = this.workersChipsVisible.indexOf(worker);

    if (chipIndex >= 0) {
      this.workersChipsVisible.splice(chipIndex, 1);
    }
  }

  ngOnInit() {
    this.form = this.fb.group({
      selectVoterOpt: [null],
      tasktitle: [null],
      taskdescription: [null],
      voterchip: [null],
      range: [null],
      type: [null, Validators.compose([Validators.required])],
      link: [null],
      textMsg: [null],
      image: [null],
      numbers: [null],
      dueDate: [null]
    });

    this.filteredWorkerOptions = this.workerAutocomplete.valueChanges.pipe(
      debounceTime(300),
      switchMap(value => this.searchFilter(value))
    );
  }

  private v_filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.voters.filter(
      option => option.toLowerCase().indexOf(filterValue) === 0
    );
  }

  private _filter(value: any): string[] {
    const filterValue = value.toLowerCase();
    return this.workers.filter(
      option =>
      (option.name.first + ' ' + option.name.last)
      .toLowerCase()
      .indexOf(filterValue) === 0
    );
  }

  workerSelected(event: any): void {
    this.workerAutocomplete.setValue(null);
    this.workerAutocomplete.reset();
    this.workerInput.nativeElement.value = '';
    if (
      !( < Array < any >> this.selectedWorkers).find(
        a => event.option && a === event.option.value
      ) &&
      this.workers.find(user => event.option && user.id === event.option.value)
    ) {
      this.selectedWorkers.push(event.option.value);
      this.workersChipsVisible.push({
        name: event.option.viewValue,
        id: event.option.value
      });
    }
  }

  private searchFilter(event): Observable < any[] > {
    console.log(this.unitFilter);
    const filterValue = event; // .target.value.toLowerCase();
    const ret = this.userService
      .authenticatedGet(
        ApiUrls.ByModel(
          'user',
          JSON.stringify({
            where: {
              ...this.unitFilter.where,
              'name.first': {
                $regex: `^.*${filterValue}.*`,
                $options: 'i'
              }
            }
          })
        )
      )
      .map(
        (res): any[] => {
          if (res.success) {
            this.workers = res.data;
            return this.workers;
          } else {
            return [];
          }
        }
      );
    return ret;
  }

  rangeActive(event: any) {
    let parts: any = [];
    let skipN: any;
    let limitN: any;

    this.range = event.target.value;

    parts = this.range.split('-');
    skipN = parts[0] - 1;
    limitN = parts[1];

    this.userService
      .authenticatedGet(
        'http://35.227.126.91/v1/voter/?skipN=' + skipN + '&limitN=' + limitN, {}
      )
      .subscribe((res: any) => {
        res.data.forEach(element => {
          this.filteredVoters.push(element);
        });
      });
  }

  onTaskSelect(select: any) {
    if (select === 'Share') {
      this.work = {
        text: this.form.value.textMsg,
        link: this.form.value.link,
        image: 'https://homepages.cae.wisc.edu/~ece533/images/monarch.png'
      };
    } else if (select === 'SMS') {
      this.work = {
        text: this.form.value.textMsg,
        numbers: this.numbers.toString()
      };
    }
  }
  private imageSrc = '';

  handleInputChange(e) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    const pattern = /image-*/;
    const reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    const reader = e.target;
    this.imageSrc = reader.result;
    console.log(this.imageSrc);
    this.form.value.image = this.imageSrc;
    this.work.image = this.imageSrc;
  }

  linkChange(e) {
    this.work.link = e.target.value;

    console.log(this.work.link);
  }

  textChange(e) {
    this.work.text = e.target.value;

    console.log(this.work.link);
  }

  onSubmit(value: any): void {
    if (this.form.valid) {
      if (this.assignmentChoice === '2') {
        this.individualAssignment();
      } else {
        if (this.bulkAssignmentChoice === '1') {
          // send all
          this.bulkAssignment(this.bulkAssignmentChoice);
        } else {
          const range = this.bulkAssignmentRange.split('-');
          if (range.length !== 2) {
            alert('invalid range');
            return;
          }
          if (isNaN(parseInt(range[0])) || isNaN(parseInt(range[1]))) {
            alert('invalid range');
            return;
          }
          // send range
          this.bulkAssignment(
            this.bulkAssignmentChoice,
            this.bulkAssignmentRange
          );
        }
      }
    }
  }
  validateRange(): boolean {
    if (!this.bulkAssignmentRange) { return true; } // because range field is not touched yet
    if (this.bulkAssignmentRange.length !== 0) { return false; }
    const range = this.bulkAssignmentRange.split('-');
    if (range.length !== 2) {
      return false;
    }
    if (isNaN(parseInt(range[0])) || isNaN(parseInt(range[1]))) {
      return false;
    }
    return true;
  }
  private bulkAssignment(aType: string, range: String = null) {
    console.log(this.unitFilter.where);
    this.userService
      .authenticatedPost(
        ApiUrls.ByModel('task/bulk'),
        JSON.stringify({
          aType,
          range,
          taskType: this.form.value.type,
          title: this.form.value.tasktitle,
          description: this.form.value.taskdescription,
          work: JSON.stringify(this.work),
          dueBy: this.form.value.dueDate,
          isDone: false,
          ...this.unitFilter.where,
          ...this.unitFilter
        }), {
          ContentType: 'application/json'
        }
      )
      .subscribe(
        res => {
          this.snackBar.open('Task created  successfully!', 'X', {
            duration: 4000
          });
          this.form.reset();
          this.dialogRef.close();
        },
        err => {
          console.log('Error occured');
          this.snackBar.open('Task creation failed !', 'X', {
            duration: 4000
          });
        }
      );
  }

  private individualAssignment() {
    this.selectedWorkers.forEach(element => {
      this.userService
        .authenticatedPost(
          ApiUrls.ByModel('task'),
          JSON.stringify({
            assignedTo: element,
            taskType: this.form.value.type,
            title: this.form.value.tasktitle,
            description: this.form.value.taskdescription,
            work: JSON.stringify(this.work),
            dueBy: this.form.value.dueDate,
            isDone: false,
            ...this.unitFilter.where
          }), {
            ContentType: 'application/json'
          }
        )
        .subscribe(
          res => {
            this.snackBar.open('Task created  successfully!', 'X', {
              duration: 4000
            });
            this.form.reset();
            this.dialogRef.close();
          },
          err => {
            console.log('Error occured');
            this.snackBar.open('Task creation failed !', 'X', {
              duration: 4000
            });
          }
        );
    });
  }

  closeDialog() {
    this.dialogRef.close();
  }
}
