import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { UserService } from '../../../services/authentication/user.service';
import { ApiUrls } from '../../../services/api.urls';
import { AgTempelateRendererComponent } from '../../utils/ag-tempelate-renderer/ag-tempelate-renderer.component';
import { GridOptions } from 'ag-grid-community';
import { CSVUploadVoterModalComponent } from '../../modals/csvupload-voter-modal/csvupload-voter-modal.component';

import { MatDialog, MatSnackBar } from '@angular/material';
import {MatInputModule} from '@angular/material/input';

import { FiltersComponent } from '../../utils/filters/filters.component';

import { BtnDeleteTableCellComponent } from '../../utils/btn-delete-table-cell/btn-delete-table-cell.component';
import { AddVoterModalComponent } from '../../modals/add-voter-modal/add-voter-modal.component';
import { ViewDetailsVoterComponent } from '../../modals/view-details-voter/view-details-voter.component';



@Component({
  selector: 'app-voters',
  templateUrl: './voters.component.html',
  styleUrls: ['./voters.component.css']
})
export class VotersComponent implements OnInit {
  @ViewChild('stateCell') stateCell: TemplateRef<any>;
  @ViewChild('districtCell') districtCell: TemplateRef<any>;
  @ViewChild('constituencyCell') constituencyCell: TemplateRef<any>;
  @ViewChild('mandalCell') mandalCell: TemplateRef<any>;
  @ViewChild('boothCell') boothCell: TemplateRef<any>;

  gridOptions: GridOptions = { };
  rowData: any[] = [];
  rowSkip = 0;
  public formattedRowData: any[] = [];
  activateDelBtn = false;
  activateEditBtn = false;

  searchString = '';
  searchClear = false;
  public rowSelection;
  public defaultColDef;
  public overlayNoRowsTemplate;

  public skipCount = 60;
  public paginationNumberFormatter;

  loadingMoreData: any = false;


  clearFilters;
  filter = {};
  searchFilter = {};

  constructor(public userService: UserService, private snackBar: MatSnackBar, private dialog: MatDialog) {
    userService.authenticatedGet(ApiUrls.GetVoters('', '', '', 60, 0), {}).subscribe((data: any) => {
      console.log(data.data);
      this.rowData = data.data;
     });

     this.rowSelection = 'multiple';
     this.defaultColDef = { resizable: true };
     this.overlayNoRowsTemplate =
     '<span class="ag-overlay-loading-center">Please wait while your rows are loading</span>';

     this.paginationNumberFormatter = function(params) {
       if ((params.value) % 60 == 0) {
        return 'more';
       } else {
       return params.value.toLocaleString();
       }
    };

  }

  onFilterChanged(filter) {
    console.log(filter);
    this.filter = filter;
  }

  onRemoveSelected() {
    const selectedData = this.gridOptions.api.getSelectedRows();
    if (confirm('Click OK to delete the selected rows')) {
      this.userService.authenticatedDelete(ApiUrls.ByModel('voter', JSON.stringify({where: {id: {$in: selectedData.map(a => a.id)}}}))).subscribe((res) => {
        this.snackBar.open('Deleted !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar']
        });
        this.gridOptions.api.updateRowData({ remove: selectedData });
      });
    }
  }

  onEditSelected() {
    const selectedRows = this.gridOptions.api.getSelectedRows();

    this.dialog.open(AddVoterModalComponent, {
      width: '450',
      data: {data: selectedRows[0], activateEditBtn: true}
    });
  }

  onViewSelected() {
    const selectedRows = this.gridOptions.api.getSelectedRows();

    this.dialog.open(ViewDetailsVoterComponent, {
      width: '600px',
      data: {data: selectedRows[0], activateEditBtn: true}
    });
  }

  onSelectionChanged(event) {
    const selectedRows = this.gridOptions.api.getSelectedRows();
    if (selectedRows.length > 0) {
      this.activateDelBtn = true;
    } else {
      this.activateDelBtn = false;
    }
    if (selectedRows.length === 1) {
      this.activateEditBtn = true;

    } else {
      this.activateEditBtn = false;

    }
  }

  onpaginationChange(params) {
    if (this.loadingMoreData || this.gridOptions.api.paginationGetTotalPages() === 1) {
      return;
    }
    if (this.gridOptions.api.paginationGetCurrentPage() === (this.gridOptions.api.paginationGetTotalPages() - 1)) {
      this.loadingMoreData = true;
      this.userService.authenticatedGet(
        ApiUrls.ByModel('voter',
         JSON.stringify({where: this.searchFilter}), this.skipCount, 50
         )).subscribe((data: any) => {
          data.data.forEach(element => {
            const next = [];
            this.rowData.push(element);
            next.push(element);
            this.gridOptions.api.updateRowData({ add: next });
          });
          this.loadingMoreData = false;
          this.skipCount += data.data.length;
      });
    }
  }

  clearFilter() {
    this.searchString = '';
    this.clearFilters = new Date();
    this.gridOptions.api.setRowData(this.rowData);
    this.searchFilter = {};
    this.skipCount = this.rowSkip;
    this.searchClear = false;
  }
  onSearch() {
    if (!this.searchClear) {
      this.rowSkip = this.skipCount;
    }
    this.searchClear = true;
    if (this.searchString === '' && Object.keys(this.filter).length === 0) {
      this.clearFilter();
      return;
    }
    const searchFilter = this.searchString !== '' ? {$text: {$search: this.searchString}} : {};
    this.searchFilter = {...this.filter, ...searchFilter};
    this.userService.authenticatedGet(
      ApiUrls.ByModel('voter', JSON.stringify({where: this.searchFilter}), 0, 25)
    ).subscribe((res: any) => {
      this.gridOptions.api.setRowData(res.data);
      this.skipCount = res.data.length;
    });
  }

  ngOnInit() {
  }

  open($event) {
    const dialogRef = this.dialog.open(CSVUploadVoterModalComponent, {
      width: '450px'
    });
  }

  ngAfterViewInit() {
    this.gridOptions.api.setColumnDefs( [
      {headerName: 'Name', cellRenderer: function(params) {
        if (params.data.meta.name.first == null || params.data.meta.name.first == '') {
          return 'Not Available';
        } else {
          return params.data.meta.name.first + ' ' + params.data.meta.name.last;
        }}, 'checkboxSelection': true, sortable: true, filter: true },
      {headerName: 'Mobile Number', cellRenderer: function(params) {
        if (params.data.mobileNumber == null || params.data.mobileNumber == '') {
          return 'Not Available';
        } else {
          return params.data.mobileNumber;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Age', cellRenderer: function(params) {
        if (params.data.age == null || params.data.age == '') {
          return 'Not Available';
        } else {
          return params.data.age;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Father\'s Name', cellRenderer: function(params) {
        if (params.data.meta.fatherName == null || params.data.meta.fatherName == '') {
          return 'Not Available';
        } else {
          return params.data.meta.fatherName;
        }}, sortable: true, filter: true },
      {headerName: 'Mother\'s Name', cellRenderer: function(params) {
        if (params.data.meta.motherName == null || params.data.meta.motherName == '') {
          return 'Not Available';
        } else {
          return params.data.meta.motherName;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Husband\'s Name', cellRenderer: function(params) {
        if (params.data.meta.husbandName == null || params.data.meta.husbandName == '') {
          return 'Not Available';
        } else {
          return params.data.meta.husbandName;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'House No.', cellRenderer: function(params) {
        if (params.data.meta.houseNumber == null || params.data.meta.houseNumber == '') {
          return 'Not Available';
        } else {
          return params.data.meta.houseNumber;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Caste', cellRenderer: function(params) {
        if (params.data.meta.caste == null || params.data.meta.caste == '') {
          return 'Not Available';
        } else {
          return params.data.meta.caste;
        }}, sortable: true, filter: true },
      {headerName: 'Occupation', cellRenderer: function(params) {
        if (params.data.meta.occupation == null || params.data.meta.occupation == '') {
          return 'Not Available';
        } else {
          return params.data.meta.occupation;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Education Qualification', cellRenderer: function(params) {
        if (params.data.meta.educationQualification == null || params.data.meta.educationQualification == '') {
          return 'Not Available';
        } else {
          return params.data.meta.educationQualification;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Gender', cellRenderer: function(params) {
        if (params.data.meta.gender == null || params.data.meta.gender == '') {
          return 'Not Available';
        } else {
          return params.data.meta.gender;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Gender Bias', cellRenderer: function(params) {
        if (params.data.meta.genderBias == null || params.data.meta.fatherNamgenderBiase == '') {
          return 'Not Available';
        } else {
          return params.data.meta.genderBias;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'State Id',
        colId: 'stateId',
        cellRendererFramework: AgTempelateRendererComponent,
        cellRendererParams: {
          ngTemplate: this.stateCell
        },
        hide: true
      },
      {headerName: 'District Id',
        colId: 'stateDistrictId',
        cellRendererFramework: AgTempelateRendererComponent,
        cellRendererParams: {
          ngTemplate: this.districtCell
        },
        hide: true
      },
      {headerName: 'Constituency Id',
        colId: 'stateConstituencyId',
        cellRendererFramework: AgTempelateRendererComponent,
        cellRendererParams: {
          ngTemplate: this.constituencyCell
        },
        hide: true
      },
      {headerName: 'Mandal Id',
        colId: 'stateMandalId',
        cellRendererFramework: AgTempelateRendererComponent,
        cellRendererParams: {
          ngTemplate: this.mandalCell
        },
        hide: true
      },
      {headerName: 'Booth Name',
        colId: 'stateBoothId',
        cellRendererFramework: AgTempelateRendererComponent,
        cellRendererParams: {
          ngTemplate: this.boothCell
        }
      },
      {headerName: 'Panchayat', cellRenderer: function(params) {
        if (params.data.panchayat == null || params.data.panchayat == '') {
          return 'Not Available';
        } else {
          return params.data.panchayat;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Aadhaar Number', cellRenderer: function(params) {
        if (params.data.aadhaarNumber == null || params.data.aadhaarNumber == '') {
          return 'Not Available';
        } else {
          return params.data.aadhaarNumber;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Voter Serial Number', cellRenderer: function(params) {
        if (params.data.meta.voterSerialNumber == null || params.data.meta.voterSerialNumber == '') {
          return 'Not Available';
        } else {
          return params.data.meta.voterSerialNumber;
        }}, sortable: true, filter: true },
      {headerName: 'Pan Number', cellRenderer: function(params) {
        if (params.data.panNumber == null || params.data.panNumber == '') {
          return 'Not Available';
        } else {
          return params.data.panNumber;
        }}, sortable: true, hide: true, filter: true },
      {headerName: 'Education Qualification', cellRenderer: function(params) {
        if (params.data.educationQualification == null || params.data.educationQualification == '') {
          return 'Not Available';
        } else {
          return params.data.educationQualification;
        }}, sortable: true, hide: true, filter: true},
      {headerName: 'Occupation', cellRenderer: function(params) {
        if (params.data.occupation == null || params.data.occupation == '') {
          return 'Not Available';
        } else {
          return params.data.occupation;
        }}, sortable: true, hide: true, filter: true},
          /*
        {headerName: "Action",
          cellRendererFramework: BtnDeleteTableCellComponent,
          colId: "delete"
        }
        */
    ]);
    this.gridOptions.api.sizeColumnsToFit();
  }

}

