import { Component, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material";
import { AddDistrictModalComponent } from "../../modals/add-district-modal/add-district-modal.component";

@Component({
  selector: "app-add-district-button",
  templateUrl: "./add-district-button.component.html",
  styleUrls: ["./add-district-button.component.css"]
})
export class AddDistrictButtonComponent implements OnInit {
  constructor(private dialog: MatDialog) {}

  ngOnInit() {}
  open($event) {
    let dialogRef = this.dialog.open(AddDistrictModalComponent, {
      width: "450px"
    });
  }
}
