import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "touppercase"
})
export class TouppercasePipe implements PipeTransform {
  transform(value: string) {
    if (value && value.length > 0) {
      return (
        value
          .toLowerCase()
          .charAt(0)
          .toUpperCase() + value.slice(1)
      );
    }
    return value;
  }
}
