import {
  Component,
  OnInit
} from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators
} from '@angular/forms';
import {
  MatDialogRef,
  MatSnackBar
} from '@angular/material';
import {
  Papa
} from 'ngx-papaparse';
import {
  ApiUrls
} from 'src/app/services/api.urls';
import {
  UserService
} from 'src/app/services/authentication/user.service';

@Component({
  selector: 'app-csvupload-voter-modal',
  templateUrl: './csvupload-voter-modal.component.html',
  styleUrls: ['./csvupload-voter-modal.component.css']
})
export class CSVUploadVoterModalComponent implements OnInit {

  file = null;
  parsedJson: any;
  public form: FormGroup;
  formSubmitted = false;
  progress = 0;
  prog = 0;
  votersLength;
  increment;
  items: Array < {
    id,
    name
  } > = [];


  constructor(public papa: Papa, public userService: UserService,
    public snackBar: MatSnackBar, public formBuilder: FormBuilder,
    public dialogRef: MatDialogRef < CSVUploadVoterModalComponent > ) {
    this.form = this.formBuilder.group({
      'stateId': ['', Validators.compose([Validators.required])],
      'stateConstituencyId': ['', Validators.compose([Validators.required])],
      // 'roles': [null, Validators.compose([Validators.required])]
    });
  }


  ngOnInit() {}
  addVoters(formvalue) {
    if (!this.file) {
      this.snackBar.open('Please add a file first!','Ok', {
        duration: 3000
      });
      return;
    }
    const form = new FormData();
    form.append('file', this.file);
    this.userService.authenticatedPost(ApiUrls.ByModel('voter/import/' + this.form.value.stateConstituencyId),
     form, {ContentType: ' '}).subscribe((res) => {
      this.snackBar.open('Successfully uploaded, voters will appear under manage/voters in few minutes','Ok', {
        duration: 3000
      });
      this.dialogRef.close();
    });
  }

  useless() {
    const booths = {};
    const addprog = 100 / (this.parsedJson.length - 1);
    this.formSubmitted = true;
    console.log('No. of voters to be aded are ', this.parsedJson.length);
    this.parsedJson.forEach((element, i) => {
        if (i === 0) {
            return;
        }
        if (booths[element[10]] === undefined) {
            booths[element[10]] = [];
        }
        booths[element[10]].push(element);
    });


    const bKeys = Object.keys(booths);
    bKeys.forEach((boothNumber) => {
      console.log('Booth number is ', boothNumber);
      booths[boothNumber].forEach((element) => {
        setTimeout(() =>
        this.userService.authenticatedGet(ApiUrls.ByModel('statebooth',
          `{where: {boothNumber: ${boothNumber}, stateConstituencyId: "${this.form.value.stateConstituencyId}"}}`)).subscribe((res) => {
            if (res.success && res.data.length > 0) {
              const booth = <any>res.data[0];
            const name = booths[9].split(' ');
            const data = {
              'meta': {
                'name': {
                  'first': name[0],
                  'last': name[1]
                },
                'fatherName': element[2],
                'motherName': element[3],
                'husbandName': element[8],
                'houseNumber': element[4],
                'gender': element[6],
                'age': Number(element[5]),
                'voterId': element[1],
                },
              'stateConstituencyNumber': parseInt(booth.stateConstituencyNumber),
              'stateMandalNumber': parseInt(booth.stateMandalNumber),
              'stateBoothNumber' : parseInt(element[11]),        // "inclinationPartyId": element[17],
              'stateBoothId': booth.id,
              'stateConstituencyId': booth.stateConstituencyId,
              'stateDistrictId': booth.stateDistrictId,
              'stateMandalId': booth.stateMandalId,
              'stateId': booth.stateId
              };
              this.userService.authenticatedPost(ApiUrls.ByModel('voter'), data, {})
                .subscribe((res) => {
                  // todo:..
                  this.progress += addprog;
                  this.prog = Math.round(this.progress);
                });
            }
        }), (this.prog + 1 + Math.random()) * 90);
      });
    });
  }

  closeDialog() {
    this.dialogRef.close();
  }


  handleInputChange(e) {
    this.file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
/*     this.papa.parse(file, {
      complete: (result) => {

        this.parsedJson = result.data;
        console.log('Parsed: ', this.parsedJson);
      }
    });
 */  }

}
