import { Component, OnInit, Input } from '@angular/core';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';
import { Router } from '@angular/router';

@Component({
  selector: 'app-task-counter',
  templateUrl: './task-counter.component.html',
  styleUrls: ['./task-counter.component.css']
})
export class TaskCounterComponent implements OnInit {

  showFilter = true;

  @Input('noFilter') set _noFilter(val) {
    this.showFilter = false;
  }


  filter = {where: {}};

  @Input('filter') set ___filter(val) {
    this.filter = val;
    this.load();
  }

  counter: number;
  doneCounter: number;

  otherCounter: {total, done} = {total: 0, done: 0};

  shareCounter: {total, done} = {total: 0, done: 0};
  callCounter: {total, done} = {total: 0, done: 0};

  smsCounter: {total, done} =  {total: 0, done: 0};

  constructor(public userService: UserService, private router: Router) {
    this.load();
  }

  ngOnInit() {
  }

  load() {
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count', JSON.stringify(this.filter))).subscribe((res) => {
      this.counter = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, isDone: true}}))).subscribe((res) => {
      this.doneCounter = res.data;
    });

    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, taskType: 'Call'}}))).subscribe((res) => {
      this.callCounter['total'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, isDone: true, taskType: 'Call'}}))).subscribe((res) => {
      this.callCounter['done'] = res.data;
    });

    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, isDone: true, taskType: 'SMS'}}))).subscribe((res) => {
      this.smsCounter['done'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, taskType: 'SMS'}}))).subscribe((res) => {
      this.smsCounter['total'] = res.data;
    });

    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, isDone: true, taskType: 'Share'}}))).subscribe((res) => {
      this.shareCounter['done'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, taskType: 'Share'}}))).subscribe((res) => {
      this.shareCounter['total'] = res.data;
    });


    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, isDone: true, taskType: 'Other'}}))).subscribe((res) => {
      this.otherCounter['done'] = res.data;
    });
    this.userService.authenticatedGet(ApiUrls.ByModel('task/count',
     JSON.stringify({where: {...this.filter.where, taskType: 'Other'}}))).subscribe((res) => {
      this.otherCounter['total'] = res.data;
    });

  }
  btn() {
    this.router.navigateByUrl('manage/workers');
  }
}
