import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  forwardRef
} from "@angular/core";
import { ENTER } from "@angular/cdk/keycodes";
import { MatChipInputEvent } from "@angular/material";
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";

const COMMA = 188;

@Component({
  selector: "app-editable-mat-chip",
  templateUrl: "./editable-mat-chip.component.html",
  styleUrls: ["./editable-mat-chip.component.css"],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => EditableMatChipComponent),
      multi: true
    }
  ]
})
export class EditableMatChipComponent implements OnInit, ControlValueAccessor {
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  @Input("errors") errors = false;
  // Enter, comma
  separatorKeysCodes = [ENTER, COMMA];

  @Input() label = "tags";
  @Input("value") _value = "";

  @Input("tags") tags: Array<any> = [];

  @Input("placeholder") placeholder: Array<any>;
  @Input("editable") editable = false;
  @Input("vertical") vertical = false;
  @Input("required") required = false;
  private pristine = true;
  onChange: any = () => {};
  onTouched: any = () => {
    // this.markAsTouched();
  };

  // @Output('tags') tagsChanged = new EventEmitter<Array<any>>();
  constructor() {}

  ngOnInit() {
    this.value = this.tags.join(",");
  }
  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our person
    if ((value || "").trim().length > 0) {
      this.tags.push(value.trim());
      this.value = this.tags.join(",");
    }

    // Reset the input value
    if (input) {
      input.value = "";
    }
  }

  remove(tag: any): void {
    const index = this.tags.indexOf(tag);
    console.log(index);
    if (index >= 0) {
      this.tags.splice(index, 1);
      this.value = this.tags.join(",");
    }
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }
  writeValue(value) {
    if (value) {
      this.value = value;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(val);
    this.onTouched();
  }
}
