import { Component } from "@angular/core";
import { ICellRendererAngularComp } from "ag-grid-angular/main";

@Component({
  selector: "app-btn-delete-table-cell",
  templateUrl: "./btn-delete-table-cell.component.html",
  styleUrls: ["./btn-delete-table-cell.component.css"]
})
export class BtnDeleteTableCellComponent implements ICellRendererAngularComp {
  public params: any;
  templateContext: { $implicit: any; params: any };

  agInit(params: any): void {
    this.params = params;
  }

  refresh(params: any): boolean {
    this.templateContext = {
      $implicit: params.data,
      params: params
    };
    return true;
  }

  public invokeDeleteMethod() {
    this.params.api.selectIndex(this.params.node.rowIndex);
    var selectedData = this.params.api.getSelectedRows();
    var res = this.params.api.updateRowData({ remove: selectedData });
    alert("Removed " + res);
    console.log(res);
  }
}
